.vote-statistics {
  background: #2a273b;
  width: 100%;
  height: calc(100% - 100px);
  padding: 19px;
  display: flex;
}
.vote-statistics .vote-statistics-left {
  transition: all 0.3s linear;
  position: relative;
  height: calc(100vh - 140px);
  flex: 1;
}
.vote-statistics .vote-statistics-left p {
  font-size: 16px;
  color: #fff;
  line-height: 16px;
  border-left: 2px solid #fff;
  margin-left: 50px;
  padding-left: 8px;
}
.vote-statistics .vote-statistics-left .vote-statistics-column {
  padding: 100px 0 0 40px;
}
.vote-statistics .vote-statistics-left .vote-statistics-column .ant-col {
  margin: 0 20px;
}
.vote-statistics .vote-statistics-left .vote-statistics-column .ant-col div {
  width: 120px;
  height: 350px;
  background: #1c1d30;
  border-radius: 5px;
}
.vote-statistics .vote-statistics-left .vote-statistics-column .ant-col h3 {
  font-size: 30px;
  color: #fff;
  text-align: center;
}
.vote-statistics-right {
  color: rgba(255, 255, 255, 0.8);
  transition: all 0.3s linear;
}
.vote-statistics-right > div {
  background: #302e45;
  width: 280px;
  padding: 0 20px;
}
.vote-statistics-right > div h3 {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.vote-statistics-right > div h3 span {
  margin: 0 5px;
}
.vote-statistics-right > div:nth-of-type(1) {
  height: 36%;
  min-height: 36%;
  margin-bottom: 20px;
}
.vote-statistics-right > div:nth-of-type(2) {
  height: calc(64% - 20px);
}
.vote-statistics-right > div:nth-of-type(2) > div {
  height: calc(100% - 45px);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.vote-statistics-right > div:nth-of-type(2) p {
  text-align: center;
}
.vote-statistics-right .ant-progress-inner {
  width: 150px !important;
  height: 150px !important;
}
.vote-statistics-right .ant-progress-text {
  color: #fff;
}
.voteInfoContent {
  width: 516px;
  margin: 0 auto;
  background: #fff;
  border-radius: 20px;
  padding-bottom: 30px;
}
.voteInfoContent .vote-bg {
  height: 34px;
  background: url("../../../../assets/images/interact/vote-info-bg.png") no-repeat;
  background-size: 100% auto;
}
.voteInfoContent .vote-modal-top {
  width: 70px;
  line-height: 65px;
  height: 70px;
  background: #d4eaf7;
  border: 2px solid #ffffff;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: -20px;
  transform: translate(-50%);
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}
.voteInfoContent .vote-info-main {
  max-height: 296px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 30px 0 15px 25px;
  overflow: auto;
  /* 定义滚动条轨道 内阴影+圆角 */
  /* 定义滑块 内阴影+圆角 */
}
.voteInfoContent .vote-info-main::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: #fff;
  border-radius: 6px;
}
.voteInfoContent .vote-info-main::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #fff;
  background-color: #fff;
  border-radius: 6px;
}
.voteInfoContent .vote-info-main::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px #333;
  border-radius: 40px;
}
.voteInfoContent .vote-info-main img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-bottom: 5px;
}
.voteInfoContent .vote-info-main div {
  margin-right: 20px;
  text-align: center;
}
.voteInfoContent .vote-info-main div:nth-child(6n) {
  margin-right: 0;
}
.voteInfoContent .ant-btn {
  width: 220px;
  height: 44px;
  background: #3A89FD;
  border-radius: 22px;
  color: #fff;
  margin-left: 150px;
}
.voteInfoWrap .ant-modal-content {
  background: none;
  box-shadow: none;
}
.vote-statisics-echarts {
  width: 100%;
}
.echarts_box {
  height: 95%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.activity_btn {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0px;
  left: 0;
}
.activity_btn .ant-btn {
  width: 220px;
  height: 44px;
  background: #3A89FD !important;
  color: #fff;
  border: none;
  border-radius: 22px !important;
  margin: 0px 10px;
  font-size: 18px;
}
.activity_btn .ant-btn.finish {
  background: #2a273b !important;
  border: solid 1px #3A89FD;
  color: #3A89FD;
  transition: none;
}
.vote-statisics-echarts {
  height: 360px;
}
.vote-statisics-echarts > div {
  height: 360px;
}
.vote-statisics-echarts canvas {
  height: 360px !important;
}
